import SavedFilters from "../../../../../e7/saved-filters.js";
import PageUtils from "../../../../page-utilities/page-utils.js";
import { Tables } from "../../../../../populate-content.js";
import { CONTEXT } from "../../../home-page-context.ts";
import { HOME_PAGE_STATES } from "../../../../page-utilities/page-state-references.ts";
import DOC_ELEMENTS from "../../../../page-utilities/doc-element-references.ts";
import { ContentManager } from "../../../../../content-manager.ts";
import ClientCache from "../../../../../cache-manager.ts";
import { getSizes, PLOT_REFS } from "../../../../../e7/plots.ts";

function addBattleTableFilterToggleListener() {
	console.log("Setting listener for filter-battle-table checkbox");
	const filterBattleTableCheckbox = DOC_ELEMENTS.HOME_PAGE.BATTLE_FILTER_TOGGLE;
	filterBattleTableCheckbox.addEventListener("click", async () => {
		console.log(
			"Toggling Filter Battle Table: ",
			filterBattleTableCheckbox.checked
		);
		const stats = await ContentManager.ClientCache.getStats();
		if (!filterBattleTableCheckbox.checked) {
			Tables.replaceBattleData(stats.battles);
		} else {
			console.log(
				"Replacing table with filtered data:",
				stats.filteredBattlesObj
			);
			Tables.replaceBattleData(Object.values(stats.filteredBattlesObj));
		}
	});
}

function addAutoZoomListener() {
	const autoZoomCheckbox = DOC_ELEMENTS.HOME_PAGE.AUTO_ZOOM_FLAG;
	autoZoomCheckbox.addEventListener("click", async () => {
		console.log("Toggling Auto Zoom: ", autoZoomCheckbox.checked);
		await ContentManager.ClientCache.cache(
			ContentManager.ClientCache.Keys.AUTO_ZOOM_FLAG,
			autoZoomCheckbox.checked
		);
	});
}

function addLatestBattlesBtnListener(stateDispatcher) {
	const latestBattlesBtn = DOC_ELEMENTS.HOME_PAGE.LATEST_BATTLES_BTN;
	latestBattlesBtn.addEventListener("click", async () => {
		console.log("Clicking Latest Battles Button");
		CONTEXT.AUTO_QUERY = true;
		CONTEXT.SOURCE = CONTEXT.VALUES.SOURCE.STATS;
		await stateDispatcher(HOME_PAGE_STATES.LOAD_DATA);
	});
}

function addPremadeFilterButtonListener(editor) {
	// Logic for adding premade filters to filter pane
	document
		.getElementById("premade-filters")
		.addEventListener("click", function (event) {
			console.log("Attempting to add a premade filter");
			event.preventDefault();
			const target = event.target.closest(".dropdown-item");
			if (!target) return;
			const filterName = target.textContent.trim();
			console.log("Target found:", filterName);
			const currStr = editor.getValue();
			const newStr = SavedFilters.extendFilters(currStr, filterName);
			editor.setValue(newStr);
		});
}

function addFilterButtonListeners(editor, stateDispatcher) {
	// Logic for submit buttons on filter pane
	const filterForm = document.getElementById("filterForm");
	filterForm.addEventListener("submit", async function (event) {
		event.preventDefault(); // Prevent actual form submission to server

		// Ensure value is synced back to textarea before submit ; not strictly necessary since processed client-side
		document.getElementById("codeArea").value = editor.getValue();

		console.log("Processing Filter Action");

		const clickedButton = event.submitter;
		const action = clickedButton?.value;
		const syntaxStr = editor.getValue();
		const appliedFilter = await ContentManager.ClientCache.getFilterStr();

		if (action === "apply") {
			const validFilter = await PageUtils.validateFilterSyntax(syntaxStr);
			if (validFilter) {
				await ContentManager.ClientCache.setFilterStr(syntaxStr);
				CONTEXT.AUTO_QUERY = false;
				CONTEXT.SOURCE = CONTEXT.VALUES.SOURCE.STATS;
				stateDispatcher(HOME_PAGE_STATES.LOAD_DATA);
				return;
			}
		} else if (action === "check") {
			console.log("Checking Str", syntaxStr);
			await PageUtils.validateFilterSyntax(syntaxStr);
			return;
		} else if (action === "clear") {
			editor.setValue("");
			console.log("Found applied filter [", appliedFilter, "] when clearing");
			if (appliedFilter) {
				console.log("Found filter str", appliedFilter);
				await ContentManager.ClientCache.setFilterStr("");
				CONTEXT.AUTO_QUERY = false;
				CONTEXT.SOURCE = CONTEXT.VALUES.SOURCE.STATS;
				stateDispatcher(HOME_PAGE_STATES.LOAD_DATA);
				return;
			}
		}
	});
}

function addPlotlyLineAndMarkWidthListener() {
	const plotDiv = DOC_ELEMENTS.HOME_PAGE.RANK_PLOT;
	if (plotDiv.__zoomListenerAttached) return;
	plotDiv.__zoomListenerAttached = true;

	console.log("Attaching plotly relayout listener");

	plotDiv.on("plotly_relayout", async function (e) {
		const ignore = CONTEXT.popKey(CONTEXT.KEYS.IGNORE_RELAYOUT);
		if (ignore) return;
		console.log("TRIGGERED PLOTLY_RELAYOUT EVENT");

		const stats = await ClientCache.getStats();

		const originalXRange = Object.values(stats.battles).length;

		const sizes = getSizes(originalXRange);

		if (e["xaxis.range[0]"] !== undefined) {
			let newRange = [e["xaxis.range[0]"], e["xaxis.range[1]"]];

			// Zoom ratio: smaller range = more zoom
			let zoomFactor = originalXRange / (newRange[1] - newRange[0]);

			// Adjust sizes proportionally (with a min/max clamp)
			// let newMarkerSize = Math.min(
			// 	Math.max(sizes.markerSize * zoomFactor, sizes.markerSize),
			// 	PLOT_REFS.markerMaxWidth
			// );
			// let newLineWidth = Math.min(
			// 	Math.max(sizes.lineWidth * zoomFactor, sizes.lineWidth),
			// 	PLOT_REFS.lineMaxWidth
			// );

			const newSizes = getSizes(newRange[1] - newRange[0]);
			const newMarkerSize = newSizes.markerSize;
			const newLineWidth = newSizes.lineWidth;

			console.log(
				"Refitting marker and line sizes to",
				newMarkerSize,
				newLineWidth
			);

			Plotly.restyle(plotDiv, {
				"marker.size": [newMarkerSize],
				"line.width": [newLineWidth],
			});
		} else {
			Plotly.restyle(plotDiv, {
				"marker.size": [sizes.markerSize],
				"line.width": [sizes.lineWidth],
			});

			console.log(
				"Reseting marker and line sizes to",
				sizes.markerSize,
				sizes.lineWidth
			);
		}
	});
}

function addStatsListeners(editor, stateDispatcher) {
	addAutoZoomListener();
	addBattleTableFilterToggleListener();
	addPremadeFilterButtonListener(editor);
	addFilterButtonListeners(editor, stateDispatcher);
	addLatestBattlesBtnListener(stateDispatcher);
}

export { addStatsListeners, addPlotlyLineAndMarkWidthListener };
